import React from 'react'
import { makeStyles, useTheme } from '@material-ui/styles'
import { fade } from '@material-ui/core/styles/colorManipulator'
import { Card, CardContent, Typography, CardMedia } from '@material-ui/core'
import { Link } from '@material-ui/icons'
import { getImageUrl, getVideoUrl } from '../../VideoUtil'

const useStyles = makeStyles(theme => ({
  card: {
    display: 'flex',
    position: 'relative',
    transition: `all 0.2s ease-in-out`,
    height: '100%',
    backgroundColor: (theme.palette.type === 'dark') ? '#333' : '#fff',
  },
  cardMedia: {
    flexShrink: 0,
    width: 0,
    [theme.breakpoints.up('sm')]: {
      width: 256
    },
    height: 350,
    transition: `all 0.5s ease-in-out`,
    '&:hover': {
      transform: `scale(1.1)`,
    },
    cursor: 'pointer'
  },
  details: {
    display: 'flex',
    flexDirection: 'column',
  },
  summary: {
    maxHeight: 400,
    margin: 0,
    overflowY: 'auto',
    textAlign: 'justify'
  },
  content: {
    flex: '1 0 auto',
  },
  controls: {
    display: 'flex',
    alignItems: 'center',
    paddingLeft: theme.spacing(4),
    paddingBottom: theme.spacing(1),
  },
  cardLink: {
    textDecoration: 'none',
    color: fade(theme.palette.common.white, 0.55),
    transition: `all 0.3s ease-in`,
    '&:hover': {
      color: fade(theme.palette.common.white, 0.75),
    }
  },
}))

function List (video) {
  const theme = useTheme()
  const classes = useStyles()
  const imgLink = getImageUrl(video, 600)

  return (
    <Card className={classes.card}>
      <Card once={true} width={600} offset={[100, 0]} overflow={true}>
        <a href={getVideoUrl(video)}>
          <CardMedia className={classes.cardMedia} image={imgLink} />
        </a>
      </Card>
      <div className={classes.details}>
        <CardContent className={classes.content}>
          <Typography component="h5" variant="h5">
            {video.attributes.source}
          </Typography>

          <Typography component="h6" variant="h6">
            {video.attributes.type}
          </Typography>

          <section className={classes.summary}>
            <Typography width="" variant="subtitle1" color="textSecondary">
              {video.attributes.description}
            </Typography>
          </section>

        </CardContent>
      </div>
    </Card>
  )
}

export default List