import React, { useState, getState, useEffect, useCallback } from 'react'
import { Grid } from '@material-ui/core'
import { Module, List, Setting } from './components'
import { sortBy, useToggle, useInput, useTitle } from './util/treeUtil'
import { debounce } from "lodash";
import { getVideos } from '../../../api/VideoApi';

/**
 * From React Showcases:
 * https://tree.valleyease.me/
 * https://github.com/ValleyZw/tree
 */

const moduleLayout = {
  xs: 12,
  sm: 6,
  md: 4,
  lg: 3,
  xl: 3
}

const listLayout = {
  xs: 12,
  sm: 12,
  md: 12,
  lg: 6,
  xl: 6
}

export default function VideoList (props) {
  const [sortValue, setLSortValue] = useState('')
  const {toggle, setToggle} = useToggle(false)
  const [searchValue, setSearchValue] = useInput()
  const [videos, setVideos] = useState(props.videos)

  useTitle('Video List');

  const layout = toggle ? listLayout : moduleLayout
  const sortedVideos = sortBy(videos.filter((p) => p.attributes.source.toLowerCase().includes(searchValue.toLowerCase())), (a, b) => b[sortValue] - a[sortValue])

  const handler = useCallback(debounce(getVideos, 600), []);
    
  useEffect(() => {
      const delayDebounceFn = setTimeout(() => {
        console.log("debounce... ", searchValue);

        handler(setVideos, searchValue, 0, 12);
      }, 500)
  
      return () => clearTimeout(delayDebounceFn)
    }, [searchValue])

  const updateSearchValue = (newValue) => {
    setSearchValue(newValue);
  }

  return (
    <>
      <Setting {...getState} searchValue={searchValue} setSearchValue={updateSearchValue} setLSortValue={setLSortValue} toggle={toggle} setToggle={setToggle} />
      <Grid container spacing={4}>
        {sortedVideos.map((video) => (
          <Grid item key={video.id} {...layout}>
            {toggle ? <List {...video}/> : <Module {...video}/>}
          </Grid>
        ))}
      </Grid>
    </>
  )
}
