// Return URL for thumbnail; URL of a scalable image, if no image resource can be found returns empty string
export function getImageUrl (rowData, width = 600) {
    if(rowData && rowData.resources && Array.isArray(rowData.resources)) {
        let filteredList = rowData.resources.filter(p => (p.type
            && p.type === "reference_keyframe"));
        let url = (Array.isArray(filteredList) && filteredList.length > 0) ? 
            filteredList[0].url
            : "UNDEFINED";

        // use scaled down image url
        if(url.indexOf("/im/") > 0) {
            url = url.replace("/im/", `/im:i:w_${width}/`);
        }

        //console.log("image url for preview: ", url);
        return url;
    } else {
        // TODO: define placeholder for missing reference key files
        return "";
    }
}

// Return MP4 URL; No HD priority in filtering a playable video, could even return empty string
export function getVideoUrl (rowData) {
    if(rowData && rowData.resources && Array.isArray(rowData.resources)) {
        let filteredList = rowData.resources.filter(p => (p.type
            && (p.type === "proxy_hd_720" || p.type === "proxy_normal")));
        let url = (Array.isArray(filteredList) && filteredList.length > 0) ? 
            filteredList[0].url
            : "UNDEFINED";

        //console.log("video url: ", url);
        return url;
    } else {
        // TODO: define placeholder for missing reference key files
        return "";
    }
}