import React, { useState, useEffect } from 'react';
import { CardContent, Card, Box } from '@material-ui/core';
import { makeStyles } from '@material-ui/styles';
import FormGroup from '@material-ui/core/FormGroup';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import Switch from '@material-ui/core/Switch';
import VideoTable from './VideoTable';
import VideoList from './list/VideoList';
import { getVideos } from '../../api/VideoApi';

// A style sheet
const useStyles = makeStyles({
    root: {
        "justify-content": "flex-start"
    }, // a style rule
    label: {}, // a nested style rule
});


export function Video() {
    const classes = useStyles();

    const [defaultView, setDefaultView] = useState(true);

    const [videoList, setVideoList] = useState('');
    const [totalCount, setTotalCount] = useState(0);

    const handleChange = (event) => {
        console.log("Switch: {}", [event.target.name]);
        setDefaultView(event.target.checked);
    }

    const updateVideoList = (data, total) => {
        console.log("Async callback updateVideoList: {}, total", data, total);
        setVideoList(data);
        setTotalCount(total);
    }

    useEffect(() => {
        if (!videoList) {
            getVideos(updateVideoList);
        }
    }, [videoList]);

    return (
        <Card className={classes.header} >
            <Box>
                <FormGroup row>
                    <FormControlLabel
                        control={
                            <Switch
                                checked={defaultView}
                                onChange={handleChange}
                                name="tableViewSwitch"
                                color="primary"
                            />
                        }
                        label="Switch View"
                    />
                </FormGroup>
            </Box>
            <CardContent>
                
                {!defaultView && videoList &&
                    <VideoTable videos={videoList} totalCount={totalCount} refresh={() => updateVideoList} />
                }
                {defaultView && videoList &&
                    <VideoList videos={videoList} totalCount={totalCount} refresh={() => updateVideoList} />
                }
            </CardContent>
        </Card>
    );
}

