import axios from 'axios';

export const getVideos = async (callback, qSearch, offset = 0, limit = 10, filterMediaType = 'Video') => {
    console.log("API q=", qSearch);

    var paramValues = (qSearch) ? {
        params: {
            "q": `${encodeURIComponent(qSearch)}`,
            "f[mediaType]": `${filterMediaType}`,
            offset: `${offset}`,
            limit: `${limit}`,
        }
    } : {
            params: {
                "f[mediaType]": `${filterMediaType}`,
                offset: `${offset}`,
                limit: `${limit}`,
            }
        }

    let res = await axios.get(
        `${process.env.REACT_APP_VIDEO_API}/content/communication`,
        paramValues
    )
        .catch(error => {
            console.log(error)
        });
    try {
        console.log("API getVideos res: ", res);
        if (Array.isArray(res.data.items)) {
            callback(res.data.items, res.data._meta.total);
        } else {
            callback([], 0);
        }
    } catch (error) {
        console.log("API call implementation looks fragile, please investigate. ", error);
        callback([], 0);
    }
};